
#header {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background: #fff;
  border-bottom: 1px solid #9a9a9a;
}
#header #header-router-view {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
#header button {
    padding: 4px;
    background: transparent;
    border: none;
    border-left: 1px solid #9a9a9a;
    font-size: 22pt;
    outline: none;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
}
#header button:active {
      background: #dbdbdb;
}
#header #menu-btn {
    border-left: none;
}
#header .fill {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border-left: 1px solid #9a9a9a;
}



