
.date-items {
  border: 1px solid #bbb;
  background: #fff;
  margin-bottom: 3px;
}
.date-items:last-child {
    margin: 0;
}
.date-items .date-items-head {
    text-align: center;
    padding: 4px;
    border-bottom: 1px solid #bbb;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.date-items .date-items-head .date {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
}
.date-items .items {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.date-items .items > li {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      position: relative;
      padding: 4px;
}
.date-items .items > li.selected {
        background: #ffe5e5;
}
.date-items .items > li.submitting {
        background: #c7eeff;
}
.date-items .items > li:nth-of-type(odd) {
        background: #f5f5f5;
}
.date-items .items > li:nth-of-type(odd).selected {
          background: #ffbbbb;
}
.date-items .items > li:nth-of-type(odd).submitting {
          background: #afe0ff;
}
.date-items .items > li .item {
        -webkit-box-flex: 1;
            -ms-flex-positive: 1;
                flex-grow: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.date-items .items > li .item .tag-list {
          -webkit-box-flex: 1;
              -ms-flex-positive: 1;
                  flex-grow: 1;
}
.date-items .items > li .item .tag-list span::after {
            content: ', ';
}
.date-items .items > li .item .tag-list span:last-child::after {
            content: '';
}
.date-items .items > li .item .item-form {
          -webkit-box-flex: 1;
              -ms-flex-positive: 1;
                  flex-grow: 1;
}
.date-items .items > li .item .price {
          margin-left: auto;
}
.date-items .items > li .item .tags {
          background: #fff;
          margin-bottom: 4px;
}
.date-items .items > li .item .tags.disabled {
            background: #ebebe4;
}
.date-items .items > li .item .tags.disabled .tag {
              background: #d3d3d0;
              color: #595959;
}
.date-items .items > li .button-group {
        margin: -4px -4px -4px 4px;
        border-left: 1px solid #9a9a9a;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
.date-items .items > li .button-group button {
          border: none;
          outline: none;
          cursor: pointer;
          -webkit-box-flex: 1;
              -ms-flex-positive: 1;
                  flex-grow: 1;
          -webkit-tap-highlight-color: transparent;
          background: transparent;
          padding: 4px;
          font-size: 1.2em;
}
.date-items .items > li .button-group button:active {
            background: rgba(0, 0, 0, 0.1);
}



