
.tags {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  border: 1px solid #ddd;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: text;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tags li.tag {
    background: #c1e2eb;
    color: #14404c;
    cursor: pointer;
}
.tags li.tag, .tags li.tag-input {
    margin: 3px 0 3px 3px;
    padding: 0 4px;
    font-size: 16px;
}
.tags li.tag, .tags li input {
    font-family: sans-serif;
    font-size: 16px;
}
.tags li input {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    background: transparent;
}
.tags.conflict li.tag-input {
    background: #ebc1c1;
}
.tags.conflict li.tag-input input {
      color: #501414;
}
.tags ul {
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    left: 0;
    right: 5%;
    max-height: 40vh;
    overflow-y: auto;
    padding: 0;
    border: 1px solid #ddd;
    list-style: none;
    z-index: 65535;
    background: white;
}
.tags ul.dropup {
      top: 0;
      max-height: 40vh;
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
      -webkit-box-orient: vertical;
      -webkit-box-direction: reverse;
          -ms-flex-direction: column-reverse;
              flex-direction: column-reverse;
}
.tags ul li {
      padding: 4px;
      font-family: sans-serif;
      cursor: pointer;
      border: 1px solid transparent;
}
.tags ul li:nth-child(odd) {
        background: #f6f6f6;
}
.tags ul li.tag-choice-selected {
        background: #c1e2eb;
}
.tags ul .tag-choice-selected {
      background: #c1e2eb;
      color: #14404c;
      border: 1px solid #14404c;
}



