
@import url(./assets/icomoon.css);
html {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14pt;
  background: #e9e9e9;
  overflow: hidden;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}
#default-router-view {
  overflow: auto;
  height: 100%;
}
#overlay {
  background: #000;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 40;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}
#overlay.visible {
    visibility: visible;
    opacity: 0.5;
}
.overlay-close {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 50;
}
@-webkit-keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
#spinner {
  position: fixed;
  visibility: hidden;
  z-index: 60;
}
#spinner::after {
    content: '';
    position: fixed;
    width: 96px;
    height: 96px;
    top: calc(50% - 48px);
    left: calc(50% - 48px);
    border-radius: 50%;
    border: 8px solid #286d81;
    border-left-color: transparent;
    -webkit-animation: spin 2s infinite linear;
            animation: spin 2s infinite linear;
}
#spinner.visible {
    visibility: visible;
}
.numeric {
  font-family: monospace;
}
.positive {
  color: #1b9c0b;
}
.loading {
  position: fixed;
  width: 100%;
}
.header-label {
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.calendar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.content {
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
}



