
#sidebar ol {
  background: #fff;
  bottom: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  left: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: 0.2s ease transform;
  transition: 0.2s ease transform;
  width: 40vw;
  z-index: 70;
}
#sidebar ol li {
    border-bottom: 1px solid #ddd;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 6px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
#sidebar ol li:hover {
      background: #dedede;
}
#sidebar ol li.router-link-active {
      background: #dedede;
}
#sidebar ol.visible {
    -webkit-transform: none;
            transform: none;
}



