
#item-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
#item-list form.item-form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
}
#item-list form.item-form .tags {
      width: 100%;
      border: 1px solid #bbb;
}
#item-list form.item-form .tags .tag {
        font-size: 14pt;
}
#item-list form.item-form .tags .tag-input input {
        font-size: 14pt;
}
#item-list form.item-form .tags .tag-input ul {
        border: 1px solid #9a9a9a;
}
#item-list form.item-form > input {
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 2px;
      border: 1px solid #bbb;
      font-size: 14pt;
}
#item-list form.item-form > input.date {
        width: 7em;
        margin-right: 4px;
}
#item-list form.item-form > input.price {
        -webkit-box-flex: 1;
            -ms-flex-positive: 1;
                flex-grow: 1;
        text-align: right;
        width: 0;
}
#item-list #date-items {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 3px;
    overflow: auto;
}
#item-list #main-item-form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    background: #e9e9e9;
    border-top: 1px solid #9a9a9a;
    padding: 3px;
}
#item-list #main-item-form .item-form {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      margin-right: 3px;
}
#item-list #main-item-form .item-form .tags {
        background: #fff;
        margin-bottom: 4px;
}
#item-list #main-item-form button[type=submit] {
      width: auto;
      background: #39a2bf;
      color: #173f4a;
      border: 1px solid #173f4a;
      font-size: 26pt;
      font-weight: bold;
      outline: none;
      cursor: pointer;
      -webkit-tap-highlight-color: transparent;
}
#item-list #main-item-form button[type=submit]:active {
        color: #39a2bf;
        background: #173f4a;
}
#item-list #main-item-form button[type=submit]:disabled {
        color: #7d7d7d;
        background: #c9c9c9;
        border: 1px solid #7d7d7d;
        cursor: default;
}
#item-list #date-items {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}



